@import './ProjectCard.module.css';

.flipCard{
    /* display: flex;
    flex-direction: column;
    border-radius: 10px;
    background: linear-gradient(180deg, #576cbc 0%, #132a53 100%);
    box-shadow: 0 16 14px 0 #041523;
    padding: 18px 24px;
    max-width: 350px;
    perspective: 1000px; */

    background: linear-gradient(180deg, #576cbc 0%, #132a53 100%);
    border-radius: 10px;
    box-shadow: 0 16 14px 0 #041523;
    padding: 18px 24px;
    width: 350px;
    height: 550px;
    
}

.flipCardInner{
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.flipCard:hover .flipCardInner {
    transform: rotateY(180deg);
  }

.flipCardFront, .flipCardBack{
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
}

.image{
    margin-bottom: 26px;
    width: 90%;
    height: 50%;
    max-width: 300px;
}

.title{
    font-size: 24px;
    font-weight: 700;
}

.description{
    margin-top: 6px;
    font-size: 24px;
    font-weight: 400;
}

.skills{
    width: 100%;
    margin-top: 14px;
    display: flex;
    flex-wrap: wrap;
    gap: 14px;
    list-style: none;
}

.skill{
    font-size: 24px;
    font-weight: 400;
    border-radius: 100px;
    background: var(--color-dark);
    padding: 2px 22px;
}


.link{
    text-decoration: none;
    color: var(--color-text);
    font-size: 30px;
    font-weight: 600;
    border-radius: 100px;
    background: var(--color-primary);
    padding: 1px 22px;
}

.learnMore{
    position: absolute;
    bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}


.flipCardBack{
    transform: rotateY(180deg);
}

.description{
    margin-left: 17px;
    font-size: 20px;
    font-weight: 300;
    text-align: left;
    letter-spacing: 1.5px;
}

.description li{
    margin-top: 9px;
}

.links{
    width: 100%;
    margin-top: 26px;
    display: flex;
    justify-content: space-around;
}
