@import '../../vars.css';

.container{
    color: var(--color-text);
    margin-top: 120px;
    background-color: var(--color-secondary);
    display: flex;
    justify-content: space-evenly;
    width: 100vw;
    padding: 58px 10%;
    gap: 10px;
}

.text h2{
    font-size: 80px;
    font-weight: 700;
    letter-spacing: 4px;
}

.links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    list-style: none;
    gap: 26px;
}

.link{
    display: flex;
    align-items: center;
    gap: 25px;
}

.link a{
    color: var(--color-text);
    text-decoration: none;
    font-size: 30px;
    font-weight: 400;
    letter-spacing: 1.5px;
}

@media (max-width: 830px){
    .container{
        flex-direction: column;
        gap: 23px;
    }

    .text{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .links{
        align-items: center;
    }
}