@import '../../vars.css';

.navbar{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 40px;
    z-index: 2;
    margin: 0 10%;
}

.title{
    text-decoration: none;
    font-size: 30px;
    font-weight: 600;
    color: var(--color-text);
}

.menu{
    display: flex;
}

.menuBtn{
    display: none;
}

.menuItems{
    display: flex;
    gap: 30px;
    list-style: none;

}

.menuItems a{
    position: relative;
    text-decoration: none;
    font-size: 24px;
    color: var(--color-text);
    padding: 4px;
}

.menuItems a::before{
    content: '';
    position: absolute;
    height: 5px;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: #fff;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform 1s ease-out;
}

.menuItems a:hover::before{
    transform: scaleX(1);
    transform-origin: left;
    transition: transform 1s ease-out;
}


@media (max-width: 830px){
    .navbar{
        flex-direction: column;
        align-items: flex-start;
    }

    .menu{
        position: absolute;
        right: 0;
        margin-right: 10%;
        flex-direction: column;
        align-items: flex-end;
        gap: 11px;
        z-index: 3;
    }

    .menuBtn{
        display: block;
        cursor: pointer;
    }

    .menuItems{
        display: none;
        flex-direction: column;
        align-items: center;
        gap: 13px;
        border-radius: 10px;
        background: rgb(25,55,109);
        /* background: linear-gradient(0deg, rgba(25,55,109,0.2) 0%, rgba(25, 55, 109, 1) 100%); */
        padding: 24px 23px;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);
    }

    .menuOpen{
        display: flex;
        z-index: 1;
    }
}