@import '../../vars.css';

.container{
    position: relative;
    background-color: rgba(12, 12, 12, 0.6);
    border-radius: 16px;
    padding: 70px;
    margin-top: 120px;
    margin-left: 10%;
    margin-right: 10%;
    z-index: 1;
}

.title{
    color: var(--color-text);
    font-size: 35px;
    font-weight: 700;
    letter-spacing: 1.75px;
    text-transform: uppercase;
}

.content{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.top{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin-top: 18px;
    color: var(--color-text);
    font-size: 26px;
    font-weight: 400;
    letter-spacing: 2.5px;
    line-height: 40px;
}

.bottom{
    display: flex;
    align-items: center;
}

.aboutImage{
    width: 45%;
}

.aboutItems{
    color: var(--color-text);
    display: flex;
    flex-direction: column;
    gap: 50px;
    list-style: none;
}

.aboutItem{
    display: flex;
    align-items: center;
    border-radius: 20px;
    padding: 25px;
    background-image: linear-gradient(90deg, rgba(165, 215, 232, 0.42) 0%, rgba(255, 255, 255, 0) 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: 0.4s;
}

.aboutItem:hover{
    background-size: 100% 100%;
}

.aboutItem h3{
    font-size: 25px;
    font-weight: 600;
}

.aboutItem p{
    font-size: 18px;
}

@media (max-width: 830px){
    .container{
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
    }

    .content{
        flex-direction: column;
    }

    .top{
        font-size: 24px;
        line-height: 30px;
        letter-spacing: 1.5px;
    }

    .aboutImage{
        display: none;
    }

    .aboutItems{
        margin-top: 29px;
    }

    .aboutItem{
        padding-left: 0;
        padding-right: 0;
    }
}